@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* CSS Variable */
:root{
--background1: #cccccc;
--background2: #9933ff;
--background3: #ff66cc;
--primary: #4d4d4d;
--background4: #006622;
}

body{
  background-color: #cccccc;
  background-color: var(--background1);
  font-size: 1.2rem;
  color: #4d4d4d;
  color: var(--primary);

}
.App{
  background-color: #9933ff;
  background-color: var(--background2);
  min-height: 35rem;
  width: 28rem;
  margin: 2.5rem auto;
}
.todo-form input{
  background-color: #ff66cc;
  background-color: var(--background3);
  border: 0;
  width: 14rem;
  height: 3rem;
  padding: 0 1.5rem;
  margin: 1.2rem;
  font-size: 1.2rem;
  border-radius: 0.5rem;
  color: #4d4d4d;
  color: var(--primary);

}
.todo-form input::-webkit-input-placeholder{
  color: #4d4d4d;
  color: var(--primary);
  font-family: 'Poppins', sans-serif;

}
.todo-form input:-ms-input-placeholder{
  color: #4d4d4d;
  color: var(--primary);
  font-family: 'Poppins', sans-serif;

}
.todo-form input::-ms-input-placeholder{
  color: #4d4d4d;
  color: var(--primary);
  font-family: 'Poppins', sans-serif;

}
.todo-form input::placeholder{
  color: #4d4d4d;
  color: var(--primary);
  font-family: 'Poppins', sans-serif;

}
.todo-form input:focus, .todo-form button:focus{
  outline: none;
}
.todo-form button{
  height: 3.5rem;
  width: 7rem;
  border-radius: 0.4rem;
  border-width: 0em;
  background-color: #006622;
  background-color: var(--background4);
  cursor: pointer;
  font-weight: bolder;
  font-size: 1.2rem;
  font-family: 'Poppins', sans-serif;
  color: #4d4d4d;
  color: var(--primary);
}
:root{
    --primary :#ffffff;
    --background: #ff66cc;
}
.list{
    color: #ffffff;
    color: var(--primary);
    width: 25rem;
    background-color: #ff66cc;
    background-color: var(--background);
    height: 2.2rem;
    margin: 1.2rem auto;
    border-radius: 0.4rem;
}
.list p{
    padding: 0.3rem 0.5rem;
    position: relative;

}
.list p span{
    position: absolute;
    right: 0.8rem;
    cursor: pointer;

}
.faIcons{
    margin: 0 0.3rem;
    font-size: 1rem;
}
.list p input{
    background-color: transparent;
    border: 0;
    color: #ffffff;
    color: var(--primary);
    font-size: 1.2rem;
    font-family: 'Poppins', sans-serif;
    width: 20rem;
}
.list p input:focus{
    outline: none;
}
